@import 'assets/styles/fonts.scss';
.main {
  margin-bottom: 40px;
}
.block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 20px 0;

  & b {
    font-weight: 500;
  }
}

.blockTitle {
  display: flex;
  margin: 4px 0;
  font-size: 18px;
  font-weight: 500;
}

.blockStep {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  margin-bottom: 20px;
}

.panelBtn {
  display: flex;
  flex-direction: row;
  justify-content: space-around;

  & .setSirensBtn {
    width: 30%;
    margin: 0;
    padding: 12px;
    border: 1px solid #333;
    border-radius: 4px;
    background-color: #09af00;

    & p {
      color: #fff;
      font-weight: 600;
    }
  }
}
