@import 'assets/styles/fonts.scss';

.main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.stepBlock{
    max-width: 800px;
    margin: 20px auto;
    text-align: center;

    // стили внутри чтобы перепидить стили от Ui
    .step_item{
        position: relative;
        width: 100%;
        margin-bottom: 20px;

        &:hover{
            box-shadow:
            0px 3px 5px -1px rgba(0,0,0,0.2),
            0px 6px 10px 0px rgba(0,0,0,0.14),
            0px 1px 18px 0px rgba(0,0,0,0.12);
            transition: box-shadow 0.4s ease;
        }
    }

    .item_img{
        position: absolute;
        top: 0;
        right: 12px;
        bottom: 0;
        display: flex;
        align-items: center;

        & img {
            height: 80px;
        }
    }
}

.PaperBlock{
    width: 100%;
    padding: 16px;
    text-align: left;
}
.PaperBlockFull {
    padding-right: 10px;
}

@media screen and (max-width: 450px) {
    .PaperBlock {
        padding-right: 90px;
    }
    .PaperBlockFull {
        padding-right: 10px;
    }
    .stepBlock {
        .item_img{
            right: 4px;
        }
    }
    .PaperBlock h2 {
        font-size: 18px;
        font-weight: 500;
    }
}
